.title-container {
  color: white;
  padding-top: 25vh;
  height: 700px;
}

.title-container h2.title {
  margin-bottom: 20px;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  font-size: 4.8em;
  line-height: 1.15em;
  font-weight: 700;
}

.title-container h4 {
  margin-bottom: 60px;
  font-family: "Roboto Slab", "Times New Roman", serif;
  font-size: 1.3em;
  line-height: 1.4em;
}

.title-container {
  position: relative;
  z-index: 999;
}

.instagram {
  position: absolute;
  margin-top: 0.25em;
  width: 100%;
  z-index: 2;
}

.instagram-overlay {
  background: rgba(0, 0, 0, 0.6);
}

.instagram-pic {
  position: relative;
  width: 16.666666%;
  height: 350px;
  object-fit: cover;
  z-index: -1;
}

.title-container .buttons a {
  padding: 17px 35px;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 400;
  background-color: #009c82;
  border: none;
}

.info-container {
  margin-top: 2em;
  margin-bottom: 2em;
}
